//Header for mobile devices
.header-mobile{
  display: none;
  height: 8rem;

  width: 100%;


  .hamburger-inner, .hamburger-inner::after, .hamburger-inner::before{
    background-color:$brand-color;
  }

  @include respond-to('medium'){
    display: block;
    position:absolute;
    top:0;
    background-color: #fff;

  }

  .header-mobile-logo{
    position: relative;
    margin:0 auto;
    img{
      height: 7rem;

      text-align: center;

    } //img
  } //header-mobile-logo

  .mobile-menu-btn{
    margin:2rem;

    &.is-active .hamburger-box{
      position:fixed;
      top:0;
      right:0;
      padding:1px;
      border-radius: 60%;
      background: rgba(255, 255, 255, 1);

      @at-root   .sb-slidebar {
       background-color: $brand-color;
      nav{
        padding-right:30px;

      }
     }

    }
  }
} //header-mobile

.header-desktop{
  position: relative;
  top: 0;
  width: 100%;
  box-shadow: none;
  @include respond-to('medium'){
    display: none;
  }
}

.header-main {
  z-index: 3;
  padding:45px;
  position:absolute;
  margin:0 auto;

  &__menu {
    display:inline-block;
    position: relative;

    &_dropdown {
      position:relative;
    $dd: &;
      &:hover{
     #{$dd}_links{      /* ul */
          display:block;
          position:absolute;
          padding:0;
            #{$dd}_item  {/* li */
            top:0px;
            padding:5px 30px;
            min-width: 245px;
            max-width: 245px;
            white-space: nowrap;
            border:2px   inset;
            border-top-color: #48d1e9;
            border-bottom-color:#1a9aba;
            background-color: transparentize($brand-color,0.1);
            &:hover{
             background-color: transparentize($brand-color,0.4);
              font-weight: 600;
            }
            #{$dd}_link{
              color: $white;
              padding-right:5px;
            }
          }
        }
      }

    } /* dropdown */
    &_link{
      color:$black;
        max-height: 35px;
        padding:8px 10px;
        &:hover, .selected & {
          background-color: transparentize($brand-color,0.1);
          color: $white;
     }
    } /*menu link*/
    &_item {
        display:inline-block;
        list-style-type: none;
        border-radius: 4px;
        color: $black;
        margin-left: 8px;
        font-size:18px;
        font-weight: 500;
        z-index: 2;
     } /*menu item*/


    &_soc{
      position:relative;
      margin-left: 1px;
      display:inline;
      &:hover{
        i,img{
        transform:scale(1.3,1.3);
        }
      }/*soc hover*/
      &_wz{
        padding-left:5px;
      }/*wz*/
        &_yt{
          color:$youtube;
          &-bg{
            color:$white;
            font-size:1.6rem;
          }/*yt bg*/
        } /*yt*/
        &_fb{
          color:$facebook;
          font-size:2rem;

      } /*fb*/
    } /*soc*/
    &_phone{
          &:hover &_icn{
             transform:rotate(220deg);
                 color:$white;
            }
      &_icn{
        padding:0 30px;
       transform:rotate(260deg);
        font-size:2rem;
        color:$brand-color;


      &_num{

        color:$black;
        font-size:18px;
      }/*num*/
    } /*icn*/
  } /*phone*/
} /*menu*/

&__logo{
  position:absolute;
  top:33px;
  left:33px;
  z-index: 3;
    max-height: 91px;
  transition:all 0.4s ease;
  &:hover{
    transform:scale(1.1,1.1);
  }
  img{
        max-height: 91px;
  }
}

} /*main header*/
