.home-trips
{
	position:relative;
top:0;
		z-index: 4;

		@include respond-to('medium')	{
					top:100px;
			}




	&__img
{
		background-repeat: no-repeat;
		background-attachment: scroll;
		background-position: center center;
		background-size: cover;
		padding: calc(100%/3) ;
		position: relative;
    height:240px;
    margin-bottom:-30px;
		&:hover{
				opacity:0.5;
				z-index:1;
				position:relative;
			  transform: scale(1.02);
 		}
/* == = large  */
		&_lg	{
		height: 510px;
		@include respond-to('medium'){
			 height:240px;
			}
	}

	@include respond-to('small')
{
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		}

		&_text{
	line-height: 1;
	width:30%;
	font-family:'Assistant',sans-serif;
	font-size:30px;
	font-weight: 800;
	position:absolute;
	top:50%;
  transform:translateY(-50%);
	left:30px;

	&>.blue-text{
		color:#00a9c3;
	}
}
&_heart{
	line-height: 1;
	width:30%;
	font-family:'Assistant',sans-serif;
	position:absolute;
	top:50%;
  transform:translateY(-5%);
	right:75px;
	text-align:center;
	font-weight: 600;
	font-size:16px;
	&>.white-text{
		color:$color-white;
		font-size:30px;
		font-weight: 800;
	}
transform:rotate(30deg);
 }
}

	/*  =end  large  */
&__text
{
	z-index: 2;
  position: relative;
	top: -30px;
	transition:all 0.5s ease;

	&_main
{
		background: transparentize($white,0.25);
		position: absolute;
 		white-space: nowrap;
    width:100%;
    padding:2.5px 7.5px 2.5px 0;

    &_lg
  {
		width:100%;
  		}
		}
	&_title
{
		color: $main-color;
		font-size: 2.5rem;
		@include respond-to('small')	{
			font-size: 2.1rem;
			line-height:3.5rem;
			}
}
	&_sub
{
		color: $alt-text-color;
		font-size: 1.6rem;
		}
	&_price
{
		background: transparentize($bg-color,0.25);
		color: $white;
		position: relative;
    float:$left;
    padding:10px 13px ;
 		text-align: center;
		&_start
{
			font-size: 1.3rem;
			}
		}
	&_num
{
		font-size: 1.9rem;
		font-weight: 700;
		}
	}

		cursor:pointer;


		}
