// -----------------------------------------------------------------------------
// This file contains all application-wide Sass variables.
// -----------------------------------------------------------------------------
/// Regular font family
/// @type List
$text-font-stack: 'Open Sans Hebrew', sans-serif !default;
$custom-font-stack: 'Helvetica Neue Light', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif !default;
/// @type Color
  $white: #ffffff !default;
  $black: #000000 !default;
  $color-white: #ffffff !default;
  $color-black: #000000 !default;
  $brand-color: #00a9c3 !default;

  $main-color:#2e6d86;
  $title-color:#2291d1;
  
  $alt-text-color:#0f7988;
  $bg-color:#00a9c3;


  //@type general
  $full-width:2000px !default;
  $max-width: 1230px !default;
  $padding-base: 15px;
  $padding-base-box: 20px;
//header
$section-spacer-height:55px !default;

//@type breakpoints
$breakpoints: (
  'mobile-m': (max-width: 375px),
  'mobile-l': (max-width: 425px),
  'extra-small': (max-width: 480px),
  'smaller': (max-width: 624px),
  'small': (max-width: 768px),
  'medium': (max-width: 992px),
  'large': (max-width: 1200px),
  'larger': (max-width: 1300px),
  'huge': (max-width: 1400px),
) !default;
//@type font sizes
$font-size-base:          16px !default;
$font-size-large:         ceil(($font-size-base * 1.25)) !default; // ~18px
$font-size-small:         ceil(($font-size-base * 0.85)) !default; // ~12px

// social colors
$twitter: #00aced;
$facebook: #3b5998;
$googleplus: #dd4b39;
$pinterest: #cb2027;
$linkedin: #007bb6;
$youtube: #bb0000;
$vimeo: #1ab7ea;
$tumblr: #32506d;
$instagram: #bc2a8d;
$flickr: #ff0084;
$dribbble: #ea4c89;
$quora: #a82400;
$foursquare: #0072b1;
$forrst: #5B9A68;
$vk: #45668e;
$wordpress: #21759b;
$stumbleupon: #EB4823;
$yahoo: #7B0099;
$blogger: #fb8f3d;
$soundcloud: #ff3a00;

@if $dir != ltr and $dir != rtl {
    $dir: ltr;
}

$left: if-ltr(right, left);
$right: if-ltr(left, right);
//footer variables
$footer-padding-top: 190px;
$footer-padding-bottom: 55px;
$footer-min-height: 800px;
