.content-main{

}
.content-banner{
      position:relative;
      z-index:4;
      margin-top:-60px;
         box-shadow: 1px 2px 25px #dfdfdf ;
         border-bottom:inset  4px solid darken(#dfdfdf, 0.2);
  &__image {

    min-height:550px;
    background-repeat:no-repeat;
    background-position:  top   center;
    background-size:cover;
    background-clip: content-box;

 	}
}
.content-sidebar{
  background: linear-gradient(to $right, #ededed 0%, #fff 50%);
  height:1600px;
  width:100%;
  padding:50px;
  h3,&__title{
    text-align: center;
    font-size:20px;
    color:$brand-color;
  }
  &__nav{
     float:$left;
  }
  &__menu{
    width:250px;
    list-style-type: none;
    background: #fff;
      padding:0;
    border-radius:  5px;
       box-shadow:0px 0px 15px #e2e2e2;
    &_item{
        padding:4px 0px;
      border-bottom:1px solid #e2e2e2;
      line-height:50px;
       &:last-of-type{
        border:0;
      }
      &_link{
          padding:2px 30px;
      }
    }
  }


 &__form{
        font-size:14px;
   &_wrapper{
     width:250px;
     list-style-type: none;
     background: #fff;
       padding:35px;
     border-radius:  5px;
        box-shadow:0px 0px 15px #e2e2e2;
   }
   &_submit{
     background-color:$brand-color;
     color:$white;
     width:100%;
     padding:15px;
     &:hover{
         background-color: transparentize($brand-color,0.5);
     }
   }
 }

 &__quote{
   &_text {
   font-size:16px;
   text-align:justify;
   color:$black;
   font-weight:bolder;
 }
   &_author{
     color:$brand-color;
   font-weight:500;
     font-size:14px;
     margin-bottom:30px;
   }

 &_left{

  width:20%;
   float:$left;
  color:$brand-color;
  background:$white;
  margin-left:65px;

}
 &_right{
  width:20%;
    float:$right;
color:$white;
background-color:$brand-color;

  margin-right: 65px;
}
 }
}

    .has-tag{
      position: relative;

      &:after{
        content: url(/assets/images/tag.png);
        position: absolute;
        left: -7px;
        top: -7px;
        z-index: 1;
      } //after

       &__text{
        font-size: 16px;
        color: #fff;
        transform: rotate(-45deg);
        width: 90px;
        position: absolute;
        top: 23px;
        left: -20px;
        z-index: 2;
        font-weight: 400;
        z-index:5;
      } //__text
    } //has-tag: ;

 .content-article {
  box-shadow: 6px -15px 25px #dfdfdf;
   background-color: #fff;
   padding:35px;

	&__about {
    &_wrapper{
      padding:0;
      width:100%;
      height:240px;
      border:1px solid #111111;
      box-shadow:5px -5px 8px #9c9c9c;
    }

		&_title {
			font-size: 21px;
			font-weight: 500;
    	color:$brand-color;


		}
	&_par {
		font-size: 16px;
		font-weight: 500;
    		color: $black;
    &_color {
        color:$brand-color;
    }
  	}
  }
}
