.home-banner {
position:relative;
z-index: 0;
  max-height:100vh;
 background-repeat: no-repeat;
@include respond-to('medium'){
 top:90px;
 background-repeat: no-repeat;

 background-color: #fff;
      max-width:$full-width;
}

  &__single img{
  min-height:100vh;

  @include respond-to('medium'){
    min-height:50vh;
  width:100%;
  background-size:contain;

       }
         }
  &__wrapper {
    position: relative;
    top: 0px;
    display:block;
     max-width:$full-width;
     padding:0px;
@include respond-to('small'){
  position: relative;
  top: 0px;

     }
     @include respond-to('extra-small'){
      }
   }


   &__navbar{
     position: relative;
     margin:0 auto;
     z-index: 7;

      &_bg{
          width: 100%;
          position: absolute;
          bottom:0px;

          background: -webkit-linear-gradient(bottom, rgba(0,0,0,1) 0%, transparent 100%);
          background: -o-linear-gradient(bottom, rgba(0,0,0,1) 0%, transparent 100%);
          background: linear-gradient(to top, rgba(0,0,0,1) 0%, transparent 100%);

          }
     &_menu{
       position: relative;
       margin: 0 auto;
       text-align: center;
       z-index:0;
       bottom:  60px;

     }
     &_item{
         transition: all 0.3s ease;
         display: inline-block;
         background:rgba(255,255,255,0);
         padding:0px;
         margin-#{$right}: 0px;
         border-radius: 50%;
       $ni: &;

         &.active{
                background:rgba(255,255,255,0.9);
                box-shadow:1px 1px 15px rgba(255,255,255,0.75);
                cursor:pointer;

                 #{$ni}_img {
                    filter:  brightness(100%);
                 }
             }
        &_link{
          display: table-cell;
           vertical-align: middle;
           height:120px;
           width:120px;
           transition: all 1s ease;
           &:hover{
            transform:scale(1.2);
           }
        }

         &_img{
           filter:  brightness(0%) invert(100%);

          }

        &_text{
          font-family:'Assistant', sans-serif;
          color:$color-white;
          font-size:18p  x;
          font-weight: 600;
          position:absolute;
          top:140px;
          width:10%;

        }
       }
     }

&__text{
  position:absolute;
  color:$color-white;
  top:250px;
  #{$right}:360px;

  & span {
    position:relative;
    margin-bottom:5px;
    border-radius: 4px;
  }

  &_title{
    font-size:30px;
    font-weight: bolder;
    position:relative;
    top:0;
    display:inline-block;
    background:transparentize($alt-text-color, 0.25);
    padding:4px 10px;
}
&_sub{
  font-size:24px;
  font-weight: bold;
  position:relative;
    display:inline-block;
  top:0;
  background:transparentize($title-color, 0.25);
  padding:5px;
}
&_arrow{

   &:hover{
     transform:scale(1.2);
     cursor:pointer;
   }
}

}
   }
