.main-footer    {
  position:relative;
  padding:200px 0 50px;
  &__link-counter{
    margin-#{$right}: 10px;
    @include respond-to('small'){
      margin: 0;
    }
    &:first-of-type{
      margin-#{$right}: -30px;
      @include respond-to('small'){
        margin: 0;
      }
      @at-root &__menu{
        padding-#{$right}: 5px;
        @include respond-to('small'){
          padding: 0;
        }
      }
    }
  }
  &__bg{
    position: absolute;
    z-index:-77;
    display:block;

    min-height:$footer-min-height;
    width:100%;
    bottom:0;
    background-repeat: no-repeat;
    background-size:100%;
    background-position: bottom center;
    @include respond-to('medium'){

    } //---large
}
&__menu{
  position: relative;

  @include respond-to('medium'){
    margin-top:90px;
    margin-bottom:45px;
  }
  &_list {
    //padding-#{$left}: 5px;
    width: 200px;
    float: #{$left};
    list-style-type:none;

    @include respond-to('large'){
      width: auto;
      float: none;
    } //---large
    @include respond-to('small'){
      padding:2px;
    }
    &:hover{
      .footer-links__border_highlight{
        transform: translateX(0);
        transition: .3s;
      }
    }
}
  &_item {
      font-weight: 300;
    }

     &_link {
    font-weight: 500;
    font-size: 14px;
    line-height: 30px;
    text-shadow: 2px 3px 3px rgba(0, 0, 0, 0.8);
    color:$white;
      padding:6px;
      @include respond-to('medium'){
       filter:inverse(100%);
        font-weight: 400;
        font-size: 9px;
          white-space: nowrap;
           color:$main-color;

      }
    @include respond-to('medium'){
      text-shadow:none;

    }

    &:hover{
       background-color: transparentize($brand-color,0.4);
        color:$white;
        }
  }
}


  &__soc{
      direction: $dir;
      position: relative;
      top: 0px;
      $soc:&;

     &_icn{
    	border-radius: 50%;
    	margin-left:11px;
    	font-size: 0.9em;
      line-height: 2;
      position: relative;
      bottom: 0;
      &.fa-facebook{
       	background: $facebook;
       padding: 0.005em 0.50em;
       max-height:25px;
       max-width:25px;
       &:hover{
         background:transparentize($facebook, 0.4);
       }
        }
       &.fa-youtube{
     	background: $youtube;
      padding: 0.005em 0.35em;
      max-height:25px;
      max-width:25px;
         &:hover{
           background:transparentize($youtube, 0.4);
         }
     	}
      &:hover{
        transform:scale(1.1,1.1);
      }
    }
      @include respond-to('medium'){
        top: unset;
        bottom: 10px;
      }
      @include respond-to('small'){
        position: absolute;
        width:100%;
        margin: 0 auto;
        display: inline-block;
        text-align: center;
        bottom:-30px;
      } //---small
      @include respond-to('extra-small'){


      }
    } //__soc


   &__contacts{
     position: relative;
    font-weight: 500;
    font-size: 14px;
    line-height: 30px;
    text-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
    color:$white;


    @include respond-to('medium'){
      float: none !important;
      padding: 0;
      margin-bottom: 20px;
      text-align:center;

    }
    @include respond-to('small'){
      padding:20px;
      font-size: 0.5rem;
      text-align:center;

    }
    @include respond-to('extra-small'){
      padding:20px;
      text-align:center;

      font-size: 1.5rem;
      color:$main-color;
    }
    &_text{
      color: #fff;
      font-weight: 400;
      font-size: 15px;
      display: inline;
      line-height: 1;
      text-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
      margin-#{$left}: 16px;
      @include respond-to('large'){
        font-size: 14px;
        margin-#{$left}: 1px;
      }
      @include respond-to('medium'){
        font-size: 14px;

      }
      @include respond-to('small'){
        font-size: 14px;
            display:inline-block;
      }
      @include respond-to('extra-small'){
      padding:0px;
      text-align: center;
    width:100%;
    }

      &:after{
        @extend .noselect;
        content: '|';
        #{$left}: -10px;
        position: relative;
        color: #fff;

        @include respond-to('large'){
          #{$left}: 0px;
        }
        @include respond-to('medium'){
          #{$left}: 0px;

        }
        @include respond-to('small'){
         display:none;
    }
        @include respond-to('extra-small'){
        display:none;
      }
      } //after

      &:last-of-type{
        &:after{
           @extend .noselect;
          content: '';
        } //after
      } //last

      } //span
    }
    a{

    }
      a.cs_logo {
        color:$white;
        padding:0 px;
        &:hover{

       }
        @include respond-to('extra-small'){
             color:$main-color;
        }


          &.cs_logo{
            transition:all 0.4s ease;
            display:block;
            float:$left;
            z-index: 11;
            &:hover{
              cursor:pointer;
              transform:scale(1.1,1.1);
                }
@include respond-to('small'){
      text-align: center;
      margin-left:50%;
      transform:translateX(-60%);
      display:block;
      filter:brightness(0%);
      position: absolute;
      bottom:0;

    }


              }
        } //a

  }
